import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, Theme, ThemeProvider } from '@mui/material/styles';
import GoogleLogin from 'react-google-login';
import AppleLogin from 'react-apple-login';
import { useState } from 'react';
import { Alert, CircularProgress } from '@mui/material';
import StateAlert from './StateAlert';



export default function SignIn(props: { theme: Theme }) {
    const [isInProgress, setIsInProgress] = useState<boolean>(false);
    const [isAuthFailed, setIsAuthFailed] = useState<boolean>(false);

    const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
        setIsInProgress(true);
        event.preventDefault();
        const data = new FormData(event.currentTarget);

        const response = await fetch('/auth/login', {

            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'accept': 'application / json'
            },
            body: JSON.stringify(Object.fromEntries(data))
        });

        if (response.status === 200) {
            const user = await response.json();
            console.log(user)
            // window.location.href = '/dashboard';
        }
        else {
            setIsInProgress(false);
            setIsAuthFailed(true);
        }

    };

    return (
        <ThemeProvider theme={props.theme}>
            {
                isInProgress ?
                    <div className='center'>
                        <CircularProgress />
                    </div >
                    :
                    <Container component="main" maxWidth="xs">
                        <CssBaseline />
                        <Box
                            sx={{
                                marginTop: 8,
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                            }}
                        >
                            <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                                <LockOutlinedIcon />
                            </Avatar>
                            <Typography component="h1" variant="h5">
                                Sign in
                            </Typography>
                            {isAuthFailed && <div style={{ width: "100%" }}>
                                <br />
                                <Alert variant="filled" sx={{ width: "100%" }} severity="error"> Wrong email or password, try again</Alert>
                            </div>
                            }
                            <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
                                <TextField
                                    margin="normal"
                                    required
                                    fullWidth
                                    id="email"
                                    label="Email Address"
                                    name="email"
                                    autoComplete="email"
                                    autoFocus
                                />
                                <TextField
                                    margin="normal"
                                    required
                                    fullWidth
                                    name="password"
                                    label="Password"
                                    type="password"
                                    id="password"
                                    autoComplete="current-password"
                                />
                                <FormControlLabel
                                    control={<Checkbox value="remember" color="primary" />}
                                    label="Remember me"
                                />
                                <Button
                                    type="submit"
                                    fullWidth
                                    variant="contained"
                                    sx={{ mt: 3, mb: 2 }}
                                >
                                    Sign In
                                </Button>
                                <GoogleLogin
                                    clientId={process.env.REACT_APP_GOOGLE_CLIENT_ID || ''}
                                    buttonText="Log in with Google"
                                    // onSuccess={handleLogin}
                                    // onFailure={handleLogin}
                                    cookiePolicy={'single_host_origin'}
                                />
                                <br />
                                <br />
                                <AppleLogin clientId={process.env.REACT_APP_APPLE_CLIENT_ID || ''} redirectURI="https://redirectUrl.com" />
                                <br />
                                <br />
                                <Grid container>
                                    <Grid item xs>
                                        <Link href="/forgot" variant="body2">
                                            Forgot password?
                                        </Link>
                                    </Grid>
                                    <Grid item>
                                        <Link href="/signup" variant="body2">
                                            {"Don't have an account? Sign Up"}
                                        </Link>
                                    </Grid>
                                </Grid>
                            </Box>
                        </Box>

                    </Container>}

        </ThemeProvider>
    );
}